<template>
  <div class="article-page">
    <nav class="my-nav van-hairline--bottom">
      <a href="javascript:;"
      @click="changeStatus('weight_desc')"
      :class="{active:sorter==='weight_desc'}"
      >推荐</a>
      <a href="javascript:;"
      @click="changeStatus(null)"
        :class="{active:sorter===null}"
      >最新</a>
      <div class="logo"><img src="@/assets/logo.png" alt="" /></div>
    </nav>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了,已经加载完了"
      @load="onLoad"
    >
      <article-item
        v-for="item in list"
        :key="item.id"
        :item="item"
      ></article-item>
    </van-list>
  </div>
</template>

<script>
// 导入请求的函数
import { getArticles } from '@/api/article'
// import ArticleItem from '@/components/ArticleItem.vue'
export default {
  // components: { ArticleItem },
  name: 'ArticlePage',
  data () {
    return {
      loading: false,
      finished: false,
      list: [],
      current: 1, // 当前页
      sorter: 'weight_desc', // 默认获取推荐的数据
      pageSize: 10// 每一页展示多少条数据
    }
  },
  // async created () {
  //   const res = await getArticles({
  //     current: this.current,
  //     sorter: this.sorter
  //   })
  //   console.log(res)
  //   this.list = res.data.rows
  // },
  methods: {
    async onLoad () {
      const res = await getArticles({
        current: this.current,
        sorter: this.sorter,
        pageSize: this.pageSize
      })
      // console.log(res)

      this.list.push(...res.data.rows)
      this.loading = false
      this.current++
      // 如果 current 的值 已经要大于等于res.data.pageTotal
      // 需要将 finished 的值设置为 true 表示已经加载完了
      if (this.current > res.data.pageTotal) {
        this.finished = true
      }
    },
    changeStatus (val) {
      this.sorter = val
      this.current = 1
      this.list = []
      this.finished = false
      this.loading = true
      // 调用onLoad函数
      this.onLoad()
    }
  }
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: "";
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
}
</style>
