import instance from '@/utils/request'

export const getArticles = (params) => {
  return instance.get('/h5/interview/query', { params })
}

export const getArticlesDetail = (id) => {
  return instance.get('/h5/interview/show', {
    params: {
      id
    }
  })
}

// 点赞
export const updateLike = id => {
  return instance({
    url: '/h5/interview/opt',
    data: {
      id,
      optType: 1
    },
    method: 'post'
  })
}
// 收藏
export const updateCollect = id => {
  return instance({
    url: '/h5/interview/opt',
    data: {
      id,
      optType: 2
    },
    method: 'post'
  })
}

// 收藏页面
export const getArticlesCollect = (obj) => {
  return instance.get('/h5/interview/opt/list', {
    params: {
      page: obj.page,
      pageSize: obj.pageSize,
      optType: 2
    }
  })
}
// 获取我的喜欢页面
export const getArticlesLike = (obj) => {
  return instance.get('/h5/interview/opt/list', {
    params: {
      page: obj.page,
      pageSize: obj.pageSize,
      optType: 1
    }
  })
}
